import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Prescriptionordertablecell.scss'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment';

class Prescriptionordertablecell extends Component {
    state = {
        onEdit: false,
        onMove: false,
        remark: this.props.value.remark,
        powerLogin: 0
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }

    on_Mouse() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: true
                }
            )
        }
    }
    on_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: false
                }
            )
        }
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    render() {
        const { value } = this.props
        console.log(value)
        const Name = value.UDGNAME.toString()
        const PName = value.UDRPNAME.toString()
        const Unit = value.UDDOSAGE.toString() + "/" + value.UDDUNIT.toString() + "/" + value.UDFREQN.toString() + "/" + value.UDROUTE.toString()
        const Status = "已結束"
        const ST = moment(value.UDBGNDT, "YYYY-MM-DD-HH:mm:ss.000000").format('YYYY/MM/DD HH:mm:ss')
        const ET = moment(value.UDENDDT, "YYYY-MM-DD-HH:mm:ss.000000").format('YYYY/MM/DD HH:mm:ss')
        console.log(this.state.remark === undefined)
        return (
            <div onMouseMove={() => this.on_Mouse()} onMouseLeave={() => this.on_leave()} className='prescriptiontablecell'>
                <div style={{ display: "grid", gridTemplateRows: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "100px" : "80px 50px"] }}>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 2fr 1fr 1fr 1fr" }}>
                        <div className='prescriptiontablecellcontext'><t4 className='prescriptiontablecellcontexttext'>{Name}</t4></div>
                        <div className='prescriptiontablecellcontext'><t4 className='prescriptiontablecellcontexttext'>{PName}</t4></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{Unit}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{Status}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{ST}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{ET}</t3></div>
                    </div>
                    <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px", display: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "none" : ""] }}>{this.state.remark}</t3>
                    <div style={this.state.onMove ? { display: this.state.powerLogin >= 2 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: [this.state.remark === undefined || this.state.remark === "" ? "100px" : "130px"], background: "white", width: "77px", border: "rgba(238,238,238,1) 1px solid" } : { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <RemarkModal caseinfo={this.props.caseinfo} type="prescription" createTime={ST} name={PName} visible={this.state.onEdit} remark={this.state.remark} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
            </div>
        );
    }
}

export default Prescriptionordertablecell
