import React, { Component } from 'react'
import * as ApiProvider from '../../API/ApiProvider'
import { DatePicker, Input } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import Nurtionfeedingcell from './Nurtionfeedingcell'
import moment from 'moment';



class Nurtionfeedingorder extends Component {
    state = {
        TRTMNTQ1: [],
        PickerDate: moment(moment().format('YYYYMMDD'), 'YYYYMMDD').valueOf()
    }
    componentDidMount() {
        this.getAPIFeedingData()
    }
    getAPIFeedingData() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID.toString().trim()
        const CID = caseinfo.CASEID.toString().trim()
        ApiProvider.getStoredProcedureTRTMNTQ1(HID, CID)
            .then(data => {
                this.setState({
                    TRTMNTQ1: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
    }
    // search_click() {
    //     const inputdata = document.getElementById("nurtion_feeding_input").value
    //     if (inputdata === '' || inputdata === undefined || inputdata === null) {
    //         console.log("No input data")
    //     }
    //     else {
    //         console.log("Click search input = " + inputdata)

    //     }

    // }
    render() {
        const title = ["開始時間", "結束時間", "處置名稱"]
        const titleCellRender = title.map(function (element, index, arr) {
            return (
                <div key={index} style={{ padding: "0px 10px", display: "flex", alignItems: "center" }}>
                    <div style={{ fontSize: "14px" }}>{element}</div>
                </div>
            );
        })
        const ListCellData = this.state.TRTMNTQ1
        const filterData = ListCellData.filter((element, index, arr) => {
            const time = this.state.PickerDate
            const starttime = moment(element.ORBGNDT, 'YYYY-MM-DD').valueOf()
            // const endtime = moment(element.ORENDDT, 'YYYY-MM-DD').valueOf()
            return starttime === time
        })
        const DataCellRender = filterData.map(function (element, index, arr) {
            return (
                <Nurtionfeedingcell key={index} data={element}></Nurtionfeedingcell>
            );
        })
        // console.log(ListCellData)
        // const { caseinfo } = this.props
        return (
            <div>
                <t1>Feeding Order</t1>
                <div style={{ display: "grid", gridTemplateColumns: "220px 1fr 25px", gridColumnGap: "10px" }}>
                    <DatePicker defaultValue={moment(this.state.PickerDate)} allowClear={false} onChange={(date, datestring) => { this.setState({ PickerDate: date.valueOf() }) }} />
                    {/* <Input id="nurtion_feeding_input" placeholder="請輸入關鍵字" /> */}
                    {/* <div onClick={() => this.search_click()} style={{ display: "flex", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div> */}
                </div>
                <div style={{ display: "grid", gridTemplateColumns: " 1fr 1fr 2fr ", background: "#cccccc", height: "50px", marginTop: "10px" }}>
                    {titleCellRender}
                </div>
                <div style={{ minHeight: "120px", maxHeight: "400px", overflowY: "auto" }}>
                    {DataCellRender}
                </div>
            </div>
        );
    }
}

export default Nurtionfeedingorder
