import React, { Component } from 'react'
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Nurtionfeedingcell.scss'
import { yyyymmddformat } from '../../Function/Timeformat'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment'

class Nurtionfeedingcell extends Component {
    state = {
        EditMode: false,
        onEdit: false,
        remark: this.props.data.FeedingOrderRemarks,
        powerLogin: 0
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }

    onMouseEnter() {
        this.setState(
            {
                EditMode: true
            }
        )
    }
    onMouseLeave() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    showModal = () => {
        this.setState({
            onEdit: true,
        });
    };

    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            reamrk: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    Moduledisplay(time, title) {
        return (
            <RemarkModal caseinfo={this.props.caseinfo} type="nurtionfeeding" createTime={time} name={title} visible={this.state.onEdit} remark={this.state.remark} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
        );
    }

    render() {
        const { data } = this.props
        const startTime = moment(data.ORBGNDT.toString().trim(), "YYYY-MM-DD").format("MM/DD")
        const endTime = moment(data.ORENDDT.toString().trim(), "YYYY-MM-DD").format("MM/DD")
        const name = data.ORPROCED.toString().trim()
        return (
            <div onMouseEnter={() => this.onMouseEnter()} onMouseLeave={() => this.onMouseLeave()} style={{ position: "relative", height: "100px" }}>
                <div className='nurtionfeedingcell' style={{ gridTemplateRows: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "1fr" : "1fr 1fr"] }}>
                    <div className='cellforstate'>
                        <div className='celllayout'>
                            <t3>{startTime}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{endTime}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{name}</t3>
                        </div>
                    </div>
                    <div className='cellforremark' style={{ display: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "none" : ""] }}>
                        <t3>{this.state.remark}</t3>
                    </div>
                </div>
                <div style={this.state.EditMode ? {
                    display: this.state.powerLogin >= 2 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", top: 0, right: 0, height: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "50px" : "100px"], background: "white", width: "77px", border: "rgba(232, 232, 232, 1) 1px solid"
                } : { display: "none" }}>
                    < div style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }} >
                        <img src={chooseedit} alt='chooseedit'></img>
                    </div>
                </div>
                {this.Moduledisplay(data.FeedingOrderStartTime, data.FeedingOrderDispositionName)}
            </div >
        );
    }
}

export default Nurtionfeedingcell
