import React, { useState, useEffect } from "react";
import { Select } from 'antd';
import Chronicvitalchart from './Chronic_vital_chart'
import * as Wardstate from '../../Function/Wardstate'
import * as SimData from '../../Data/SimData'
import * as Timeformat from '../../Function/Timeformat'
import * as ApiProvider from '../../API/ApiProvider'
import moment from 'moment';
import './Chronic_vital.scss'


export function ChronicVital(props) {
    const [StatusSource, SetStatusSource] = useState('CM')
    const [Hisid, SetHisid] = useState("")
    const [CenterMonitorValue, SetCenterMonitorValue] = useState(SimData.CenterMonitorNewData)
    const [hrdatasource, Sethrdatasource] = useState([])
    const [rrdatasource, Setrrdatasource] = useState([])
    const [btdatasource, Setbtdatasource] = useState([])
    const [spo2datasource, Setspo2datasource] = useState([])
    const [abpddatasource, Setabpddatasource] = useState([])
    const [nbpdatasource, Setnbpddatasource] = useState([])
    const [defaultdate, Setdefaultdate] = useState(24)
    const [interval, Setinterval] = useState(0)
    // const Datasource = switchdata(props.select_interval, StatusSource, props.PickerDate)
    useEffect(() => {
        const ID = props.caseinfo.HISID.toString()
        SetHisid(ID)
        const Default = props.PickerDate
        Setdefaultdate(Default)
        const Inter = props.select_interval
        Setinterval(Inter)

    })
    // useEffect(() => {
    //     ApiProvider.getHisidwardvalue(Hisid)
    //         .then(data => {
    //             console.log(data.data)
    //             SetCenterMonitorValue(data.data);
    //         })
    //         .catch(err => {
    //             console.log("Andy test getHisidwardvalue NG " + JSON.stringify(err));
    //         });
    // }, [Hisid]);

    useEffect(() => {
        function intervalSelect(vitalday, defaultdate) {
            if (vitalday === 0) {
                const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const startTime = moment(defaultdate + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const interval = 24
                return {
                    "endTime": endTime,
                    "startTime": startTime,
                    "interval": interval
                }
            }
            if (vitalday === 1) {
                console.log("123987656789")
                const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const startTime = moment(defaultdate + (31 * 60 * 60 * 1000) - ((24 * 3 * 60 * 60 * 1000))).format("YYYYMMDDHHmmss");
                const interval = 18
                return {
                    "endTime": endTime,
                    "startTime": startTime,
                    "interval": interval
                }
            }
            if (vitalday === 2) {
                const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const startTime = moment(defaultdate + (31 * 60 * 60 * 1000) - ((24 * 7 * 60 * 60 * 1000))).format("YYYYMMDDHHmmss");
                const interval = 21
                return {
                    "endTime": endTime,
                    "startTime": startTime,
                    "interval": interval
                }
            }
            const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
            const startTime = moment(defaultdate + (31 * 60 * 60 * 1000) - ((24 * 14 * 60 * 60 * 1000))).format("YYYYMMDDHHmmss");
            const interval = 14
            return {
                "endTime": endTime,
                "startTime": startTime,
                "interval": interval
            }
        }
        const defaultdate = props.PickerDate
        const interval = props.select_interval
        const optionvalue = intervalSelect(interval, defaultdate)
        // const hisid = "TEST1234"
        const hisid = props.caseinfo.HISID.toString().trim()
        console.log(interval, defaultdate)
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "hr")
            .then(data => {
                // this.setState({
                //     hrdatasource: data.data
                // })
                Sethrdatasource(data.data)
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "rr")
            .then(data => {
                Setrrdatasource(data.data)
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "bt")
            .then(data => {
                Setbtdatasource(data.data)
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "spo2")
            .then(data => {
                Setspo2datasource(data.data)
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "abpd")
            .then(data => {
                Setabpddatasource(data.data)
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "nbpd")
            .then(data => {
                Setnbpddatasource(data.data)
            })
            .catch(err => {
                console.log(err);
            })
    }, [interval]);


    function GridNumber(interval) {
        switch (interval) {
            case 0:
                return "repeat(24,1fr)"
            case 1:
                return "repeat(18,1fr)"
            case 2:
                return "repeat(21,1fr)"
            case 3:
                return "repeat(14,1fr)"
            default:
                return null
        }
    }
    function switchdata(intervalTime, statussource, defaultdate) {
        //intervalTime 0:1天 1:3天 2:一週 3兩週
        function endFormat(intervaltime) {
            if (intervaltime === 0) {
                return 24 * 60 * 60 * 1000
            }
            if (intervaltime === 1) {
                return 24 * 60 * 60 * 1000 * 3
            }
            if (intervaltime === 2) {
                return 24 * 60 * 60 * 1000 * 7
            }
            return 24 * 60 * 60 * 1000 * 14
        }
        function changevitaldays(intervaltime) {
            if (intervaltime === 0) {
                return 24
            }
            if (intervaltime === 1) {
                return 18
            }
            if (intervaltime === 2) {
                return 21
            }
            return 14
        }
        const endTime = defaultdate + (31 * 60 * 60 * 1000)
        const startTime = endTime - endFormat(intervalTime)
        if (statussource === 'NIS') {
            const datatime = Timeformat.formatxArrayhalf(startTime, changevitaldays(intervalTime))
            const data = Wardstate.simulationnisdata(datatime, changevitaldays(intervalTime))
            return data
        }
        else {

            const datatime = Timeformat.formatxArrayhalf(startTime, changevitaldays(intervalTime))
            const data = Wardstate.simulationCentermonitordata(datatime, changevitaldays(intervalTime))
            return data
        }
    }
    const { Option } = Select;
    return (
        <div>
            <div style={{ display: "flex", alignItems: "center", height: "50px" }}>
                <t1>生命徵象</t1>
                <Select defaultValue={StatusSource} style={{ width: 200, marginLeft: "20px" }} onChange={(value) => SetStatusSource(value)}>
                    <Option value={'CM'}>Center Monitor</Option>
                    <Option value={'NIS'}>NIS</Option>
                </Select>
            </div>
            <div style={{ display: "grid", gridTemplateColumns: "90px 1fr" }}>
                <div style={{ display: "grid", gridTemplateRows: "repeat(6,60px)" }}>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div>
                            <t4 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("HR") }}>{"HR"}</t4>
                            <t1 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("HR") }}>{CenterMonitorValue.hr}</t1>
                        </div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div>
                            <t4 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("ABP") }}>{"ABP"}</t4>
                            <t3 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("ABP") }}>{CenterMonitorValue.abps}/{CenterMonitorValue.abpd}</t3>
                            <t3 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("ABP") }}>{CenterMonitorValue.abpm}</t3>
                        </div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div>
                            <t4 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("NBP") }}>{"NBP"}</t4>
                            <t3 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("NBP") }}>{CenterMonitorValue.nbps}/{CenterMonitorValue.nbpd}</t3>
                            <t3 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("NBP") }}>{CenterMonitorValue.nbpm}</t3>
                        </div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div>
                            <t4 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("SpO2") }}>{"SpO2"}</t4>
                            <t1 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("SpO2") }}>{CenterMonitorValue.spo2}</t1>
                        </div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div>
                            <t4 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("BT") }}>{"BT"}</t4>
                            <t1 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("BT") }}>{CenterMonitorValue.bt}</t1>
                        </div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div>
                            <t4 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("RR") }}>{"RR"}</t4>
                            <t1 style={{ display: "flex", justifyContent: "center", color: Wardstate.statecolor("RR") }}>{CenterMonitorValue.rr}</t1>
                        </div>
                    </div>
                </div>
                <div className='Chronicvitalchary'>
                    <div className='grid'>
                        <div style={{ display: "grid", gridTemplateColumns: GridNumber(props.select_interval) }}>
                            {/* {this.Interval_grid_box()} */}
                        </div>
                    </div>
                    <Chronicvitalchart statussource={StatusSource} datasource={hrdatasource} max={200} min={0} select_interval={props.select_interval} select_date={props.PickerDate} id_key="hr_id_chronic" svg_id="hr_svg_chronic" circlrcolor={"#779F50"}></Chronicvitalchart>
                    <Chronicvitalchart statussource={StatusSource} datasource={abpddatasource} max={200} min={0} select_interval={props.select_interval} select_date={props.PickerDate} id_key="abp_id_chronic" svg_id="abp_svg_chronic" circlrcolor={"#C12528"}></Chronicvitalchart>
                    <Chronicvitalchart statussource={StatusSource} datasource={nbpdatasource} max={200} min={0} select_interval={props.select_interval} select_date={props.PickerDate} id_key="nbp_id_chronic" svg_id="nbp_svg_chronic" circlrcolor={"#C12528"}></Chronicvitalchart>
                    <Chronicvitalchart statussource={StatusSource} datasource={spo2datasource} max={200} min={0} select_interval={props.select_interval} select_date={props.PickerDate} id_key="spo2_id_chronic" svg_id="spo2_svg_chronic" circlrcolor={"#4773B8"}></Chronicvitalchart>
                    <Chronicvitalchart statussource={StatusSource} datasource={btdatasource} max={200} min={0} select_interval={props.select_interval} select_date={props.PickerDate} id_key="bt_id_chronic" svg_id="bt_svg_chronic" circlrcolor={"#7F3DB5"}></Chronicvitalchart>
                    <Chronicvitalchart statussource={StatusSource} datasource={rrdatasource} max={200} min={0} select_interval={props.select_interval} select_date={props.PickerDate} id_key="rr_id_chronic" svg_id="rr_svg_chronic" circlrcolor={"#DE7531"}></Chronicvitalchart>
                </div>
            </div>
        </div>
    );
}